import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceProviderService } from '../core/service-provider.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-provider-home',
  templateUrl: './provider-home.page.html',
  styleUrls: ['./provider-home.page.scss'],
})
export class ProviderHomePage implements OnInit {
  services = [];
  constructor(
    public router: Router,
    private service: ServiceProviderService,
    private utility: UtilityService
  ) {}

  ngOnInit() {
    this.getServices();
  }

  ionViewDidEnter() {
    // this.services = JSON.parse(localStorage.getItem('userObj')).services;
    console.log(this.services);
  }

  getServices() {

    var userId = JSON.parse(localStorage.getItem('userObj'))._id;
    this.service.getServicesOfProvider(userId).subscribe((res) => {
      console.log(res);
      this.services = res;
    });
  }

  viewService(service) {
    localStorage.setItem('service', service);
    this.utility.navigate('/tabs/service-details?id=' + service._id);
  }
}
